<template>
  <div class="fileApp">
    <v-card>
      <v-card-title class="text-center justify-center py-6">
        <h1 class="font-weight-bold text-h2 basil--text">
          FILE
        </h1>
      </v-card-title>

      <v-tabs
        v-model="tab"
        background-color="transparent"
        grow
      >
        <v-tab
          v-for="item in items"
          :key="item"
        >
          {{ item }}
        </v-tab>
      </v-tabs>

      <v-tabs-items v-model="tab">
        <v-tab-item class="tabTitle">
          <h2>    </h2>
          <v-row>
            <v-col>
              <v-card
                flat
              >
              <v-card-text>
                <v-file-input
                  v-model="userFile"
                  name="file"
                  counter
                  show-size
                  truncate-length="50"
                  type="file"
                  label=" "
                ></v-file-input>
              </v-card-text>
          </v-card>
            </v-col>
            <v-col>
              <v-btn
              color="#623b2c"
              @click="upload"
              dark
              :loading="upLoadBtn"
              :disabled="upLoadBtn"
              >
                
              </v-btn>
            </v-col>
          </v-row>
        </v-tab-item>

        <v-tab-item>
          <v-card
            flat
          >
            <v-btn
              class="mx-2"
              fab
              dark
              small
              color="pink"
              @click="reload"
            >
              <v-icon dark>
              mdi-refresh
              </v-icon>
            </v-btn>
            <v-card-text>
              <v-img
              v-for="(v,i) in imgArray"
              :key="i"
              max-height="150"
              max-width="250"
              :id="imgArray[i]"
              @click="selectImg(imgArray[i], i)"
              :class="{ active: isActive[i] }"
              :src="`https://cats0713.keyway.dev/api/download?filename=${imgArray[i]}`"
              alt="."
              ></v-img>
            </v-card-text>
          </v-card>
        </v-tab-item>
      </v-tabs-items>
    </v-card>
    <v-alert
      v-if="!card.cardStatus"
      :type="card.cardType"
      dark
      >
    {{card.cardText}}
    </v-alert>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        card: {
          cardStatus: true,
          cardText: '',
          cardType: '',
        },
        tab: null,
        items: ['upload', 'download'],
        userFile: null,
        imgArray: [],
        upLoadBtn: false,
        isActive: [],
      }
    },
    methods: {
      upload () {
        if (this.userFile == null) {
          this.card.cardText = '  '
          this.card.cardType = 'error'
          this.card.cardStatus = false
          setTimeout(() => {
            this.card.cardStatus = true
          }, 3000)
        } else {
          this.upLoadBtn = true
          const formData = new FormData()
          formData.append('file', this.userFile)
          this.$axios.post('/api/upload', formData, { headers: { 'Content-Type': 'multipart/form-data' } }).then(res => {
            this.userFile = null
            this.card.cardText = ' '
            this.card.cardType = 'success'
            this.card.cardStatus = false
            setTimeout(() => {
              this.card.cardStatus = true
              this.upLoadBtn = false
            }, 3000)
          }).catch(err => {
            console.log(err)
          })
        }
      },
      reload () {
        this.imgArray = []
        this.$axios.get('/api/download/all').then((res) => {
          this.imgArray = Object.assign(res.data)
        })
      },
      selectImg (e, i) {
        console.log(e)
        this.isActive[i] = true
      },
    },
    mounted () {
      this.$axios.get('/api/download/all').then((res) => {
        // console.log(typeof (res))
        this.imgArray = Object.assign(res.data)
        this.imgArray.forEach((v, i) => {
          this.isActive[i] = false
        })
        // console.log(this.imgArray)
        // console.log(`https://cats0713.keyway.dev/api/download?filename=${this.imgArray[0]}`)
        console.log(this.isActive)
      })
    },
  }
</script>

<style lang="scss" scoped>
  .tabTitle{
    text-align: center;
    h2{
      margin: 20px;
      display: inline-block;
    }
  }
  .v-image{
    display: inline-block;
  }
  .v-image:hover{
    cursor: pointer;
    filter: blur(5px);
  }
  .active{
    filter: blur(5px);
  }
</style>
